import React from "react";

import "./ShippingInformation.css";

import deliveryIcon from "../assets/delivery.svg";
import billIcon from "../assets/bill.svg";
import headsetIcon from "../assets/headset.svg";
import walletIcon from "../assets/wallet.svg";

export default function ShippingInformation() {
  return (
    <section className="Shipping-information">
      <div>
        <img src={deliveryIcon} alt="delivery" />
        <ul>
          <li>Free delivery</li>
          <li>From R1000</li>
        </ul>
      </div>
      <div>
        <img src={billIcon} alt="bill" />
        <ul>
          <li>Free Return</li>
          <li>365 days</li>
        </ul>
      </div>
      <div>
        <img src={headsetIcon} alt="delivery" />
        <ul>
          <li>Support 24/7</li>
          <li>Online 24 hours</li>
        </ul>
      </div>
      <div>
        <img src={walletIcon} alt="delivery" />
        <ul>
          <li>Big Saving</li>
          <li>Weekend Sales</li>
        </ul>
      </div>
    </section>
  );
}
