import React, { useState } from "react";
import { products } from "../../data/products";

import "./OnSellProduct.css";

// import loveIcon from "../assets/heart-white.svg";
// import image1 from "../assets/WK828R.jpg";
import SingleProduct from "../SingleProduct/SingleProduct";
import ProductCard from "../ProductCard/ProductCard";

export default function OnSellProduct() {
  const [productId, setSetProductId] = useState(null);
  const [viewDetails, setViewDetails] = useState(null);
  return (
    <>
      <section className="Onsell-products">
        <h2>Onsell Products</h2>
        {products.slice(0, 6).map((product) => (
          <ProductCard
            key={product.product_id}
            {...product}
            onSetProductId={setSetProductId}
            viewDetails={viewDetails}
            onViewDetails={setViewDetails}
          />
        ))}
      </section>
      {products.length >= 1 && productId && (
        <SingleProduct productId={productId} onSetProductId={setSetProductId} />
      )}
    </>
  );
}
