import React, { useState } from "react";

import "./AppHeader.css";

import menuIcon from "../assets/menu-icon.svg";
import closeMenuIcon from "../assets/close-menu-icon.svg";
import searchIcon from "../assets/search.svg";
import heartIcon from "../assets/heart.svg";
import cartIcon from "../assets/bag.svg";
import bafKhoLogo from "../assets/bafkho-logo.png";
import { NavLink, Link } from "react-router-dom";

export default function AppHeader() {
  const [addClass, setAddClass] = useState("close-menu");

  return (
    <header className="App-header">
      <p>Get 50% discount for this summer</p>
      <nav>
        <h1>
          {addClass === "close-menu" && (
            <img
              src={menuIcon}
              className="menu"
              alt="menu"
              onClick={() => setAddClass("open-menu")}
            />
          )}
          {addClass === "open-menu" && (
            <img
              src={closeMenuIcon}
              className="animate__animated animate__flipInX menu"
              alt="menu"
              onClick={() => setAddClass("close-menu")}
            />
          )}
          <span>
            <Link to="/bafkho">
              <img src={bafKhoLogo} alt="logo" />
            </Link>
          </span>
        </h1>
        <ul
          className={`navigation animate__animated animate__slideInRight animate__faster  ${addClass} `}
        >
          <li>
            <NavLink onClick={() => setAddClass("close-menu")} to="/services">
              Services{" "}
            </NavLink>
          </li>
          <li>
            <NavLink onClick={() => setAddClass("close-menu")} to="/shop">
              Shop{" "}
            </NavLink>
          </li>
          <li>
            <NavLink onClick={() => setAddClass("close-menu")} to="/gallery">
              Gallery{" "}
            </NavLink>
          </li>
          <li>
            <NavLink onClick={() => setAddClass("close-menu")} to="/blog">
              Blog{" "}
            </NavLink>
          </li>
          <li>
            <NavLink onClick={() => setAddClass("close-menu")} to="/contact">
              Contact{" "}
            </NavLink>
          </li>
        </ul>
        <ul className="account">
          <li>
            <img src={searchIcon} alt="search" />
          </li>
          <li>
            <NavLink onClick={() => setAddClass("close-menu")} to="/bafkho">
              <img src={heartIcon} alt="wishlist" />
            </NavLink>
          </li>
          <li>
            <NavLink onClick={() => setAddClass("close-menu")} to="/bafkho">
              <img src={cartIcon} alt="cart" />
            </NavLink>
          </li>
          <li>
            <NavLink onClick={() => setAddClass("close-menu")} to="/bafkho">
              Login{" "}
            </NavLink>
          </li>
        </ul>
      </nav>
    </header>
  );
}
