import React, { useState } from "react";

import "./SingleProduct.css";

import cancelIcon from "../assets/cancel.svg";
import { products } from "../../data/products";

export default function SingleProduct({
  onShowDetails,
  productId,
  onSetProductId,
}) {
  const [imageNumber, setImageNumber] = useState(0);
  const product = products.find((product) => product.product_id === productId);
  return (
    <section
      className="Single-product animate__animated animate__slideInUp animate__faster"
      onClick={onShowDetails}
    >
      <div onClick={() => onSetProductId(null)}></div>
      <article>
        <div>
          <h2>
            <span>{product.title.slice(0, 20)}</span>

            <img
              onClick={() => onSetProductId(null)}
              src={cancelIcon}
              alt="close"
            />
          </h2>
          <h4>{product.brand}</h4>

          <h3>{product.pricing.regular_price}</h3>
          <div>
            <ul>
              <li>Description</li>
              <li>Details</li>
            </ul>
            <p>{product.description.substring(0, 120)}</p>
          </div>
          <div>
            {
              <div>
                <h4>Color</h4>
                <ul>
                  {product.varialble_product.color.map((varialble, i) => (
                    <li key={i}>{}</li>
                  ))}
                </ul>
              </div>
            }
            <div>
              <h4>Quantity</h4>
              <ul>
                <li>-</li>
                <li>5</li>
                <li>+</li>
              </ul>
            </div>
          </div>
          <h4>
            <span>Total price</span>
            <span>R1500</span>
          </h4>
          <button>Add to Cart</button>
        </div>
        <div>
          <figure>
            {product.image_demostrations.length >= 1 ? (
              <img
                src={product.image_demostrations[imageNumber]}
                alt="product"
              />
            ) : (
              <img src={product.product_image} alt="product" />
            )}
          </figure>
          {product.image_demostrations.length >= 1 && (
            <ul>
              <li onClick={() => setImageNumber(0)}>
                {imageNumber === 0 && <span></span>}
              </li>
              <li onClick={() => setImageNumber(1)}>
                {imageNumber === 1 && <span></span>}
              </li>
              <li onClick={() => setImageNumber(2)}>
                {imageNumber === 2 && <span></span>}
              </li>
              <li onClick={() => setImageNumber(3)}>
                {imageNumber === 4 && <span></span>}
              </li>
              <li onClick={() => setImageNumber(4)}>
                {imageNumber === 4 && <span></span>}
              </li>
            </ul>
          )}
        </div>
      </article>
    </section>
  );
}
