import React from "react";

import "./ProductCard.css";

import loveIcon from "../assets/heart-white.svg";

export default function ProductCard({
  product_id,
  product_image,
  title,
  brand,
  description,
  pricing,
  onSetProductId,
  onViewDetails,
  viewDetails,
}) {
  console.log(viewDetails);
  return (
    <div onMouseOver={() => onViewDetails(product_id)} className="Product-card">
      <header>
        <h3>
          <span>{title}</span>
          {/* <span>Sale</span> */}
        </h3>
        <img src={product_image} alt="product" />
        <h4 onClick={() => onSetProductId(product_id)}>
          {viewDetails === product_id && (
            <span className="animate__animated animate__fadeIn animate__slow">
              View Details
            </span>
          )}
        </h4>
      </header>
      <h3>R {pricing.regular_price}</h3>
      <figure>
        <img src={loveIcon} alt="add to cart" />
      </figure>
      <footer>
        <h4>{brand}</h4>
        <p>{description.slice(0, 140)}</p>
        <button>Add to Cart</button>
      </footer>
    </div>
  );
}
