import React from "react";

import "./AppFooter.css";

import facebook from "../assets/facebook.svg";
import twitter from "../assets/twitter.svg";
import instagram from "../assets/instagram.svg";
import whatsapp from "../assets/whatsapp.svg";
import creditCheque from "../assets/credit_cheque.png";
import mobiCred from "../assets/mobicred.png";
import zapper from "../assets/zapper.png";
import masterpass from "../assets/masterpass.png";
import visaElectron from "../assets/visa-electron-and-maestro.png";

export default function AppFooter() {
  return (
    <footer className="App-footer">
      <div>
        <div>
          <h3>Payment Methods</h3>
          <p>
            Debit cards issued by Maestro and Visa Electron are still popular
            among many South African shoppers, and can be used to make online
            purchases.
          </p>
        </div>
        <ul>
          <li>
            <img src={creditCheque} alt="credit card" />
          </li>
          <li>
            <img src={mobiCred} alt="mobicred" />
          </li>
          <li>
            <img src={masterpass} alt="masster pass" />
          </li>
          <li>
            <img src={zapper} alt="zapper" />
          </li>
          <li>
            <img src={visaElectron} alt="visa" />
          </li>
        </ul>
      </div>
      <div>
        <form>
          <input type="email" name="email" placeholder="Your e-mail" />
          <input type="submit" value="Subscribe Now" />
        </form>

        <div>
          <div>
            <h4>Partnership</h4>
            <ul>
              <li>
                <a href="/">Websites</a>
              </li>
              <li>
                <a href="/">Social Media</a>
              </li>
              <li>
                <a href="/">Branding</a>
              </li>
            </ul>
          </div>
          <div>
            <h4>About</h4>
            <ul>
              <li>
                <a href="/">Our projects</a>
              </li>
              <li>
                <a href="/">Shop</a>
              </li>
            </ul>
          </div>
          <div>
            <h4>Support</h4>
            <ul>
              <li>
                <a href="/">Request a quote</a>
              </li>
              <li>
                <a href="/">Contact</a>
              </li>
            </ul>
          </div>
        </div>
        <div>
          <h4>All rights reserved 2020</h4>
          <ul>
            <li>
              <a href="/">
                <img src={facebook} alt="facebook" />
              </a>
            </li>
            <li>
              <a href="/">
                <img src={twitter} alt="twitter" />
              </a>
            </li>
            <li>
              <a href="/">
                <img src={instagram} alt="instagram" />
              </a>
            </li>
            <li>
              <a href="/">
                <img src={whatsapp} alt="whatsapp" />
              </a>
            </li>
          </ul>
        </div>
      </div>
    </footer>
  );
}
